<script lang="ts">import { ModesModel } from '../models/models';
import { Mode } from '../store';
// TODO: make functional and put in helper
$: checkboxes = Object.entries(ModesModel).map(([key, value]) => ({
    label: key,
    isSelected: value === $Mode,
}));
</script>

<div class="ModeSwitch">
  {#each checkboxes as { label, isSelected } (label)}
    <div class="container" on:click={Mode.toggle}>
      <div class="checkbox" class:checkbox--isSelected={isSelected} />
      <span class="label">{label}</span>
    </div>
  {/each}
</div>

<style lang="scss">.ModeSwitch {
  border: 1px solid;
  left: 0;
  padding: 0.8rem;
  position: absolute;
  top: 0;
}

.container {
  display: flex;
}

.checkbox {
  border-radius: 50%;
  border: 1px solid;
  cursor: pointer;
  height: 1.6rem;
  margin-right: 0.8rem;
  width: 1.6rem;
}
.checkbox--isSelected {
  background-color: blue;
}

.label {
  cursor: pointer;
}</style>
