<script lang="ts">import Item from './Item.svelte';
import Input from './Input.svelte';
import Storage from './Storage.svelte';
import ModeSwitch from './ModeSwitch.svelte';
import { Items, ItemsByGroup, Groups } from '../store';
import { DirectionModel } from '../models/models';
// TODO: should think of a way to deal with these composite actions elsewhere perhaps...?
const move = (groupId, direction) => {
    Items.removeAllChildren();
    Groups.reorderGroup(groupId, direction);
};
const deleteGroup = (groupId) => {
    Items.removeAllChildren();
    Groups.deleteGroup(groupId);
};
</script>

<main class="Main">
  <ModeSwitch />
  <Storage />

  <h1 class="appHeader">MANAGER</h1>

  {#each $ItemsByGroup as { groupId, items } (groupId)}
    <h2 class="groupHeader">{$Groups[groupId].label}</h2>

    <Input
      on:add={({ detail }) => Items.addItem(groupId, detail)}
      on:delete={() => deleteGroup(groupId)}
      on:move={({ detail }) => move(groupId, detail)}
    />

    <div class="items">
      {#each items as itemId (itemId)}
        <Item {itemId} {groupId} />
      {/each}
    </div>
  {/each}
</main>

<style lang="scss">.Main {
  align-items: center;
  border: 5px solid;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  max-width: 100rem;
}

